.Header{
    height: 10%;
    display: flex;
}

.image{
    width: 30%;
}

.image img{
    width: 100%;
    height: 99%;
}

.red{
    width: 55%;
    display: flex;
    background-color: #cf2c31;
    color: white;
}

.part1{
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
}

.part1 p{
    font-size: 100%;
}

.part2{
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.part2 span{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
}

.part2 span > *{
    font-size: 100%;
}

.blue{
    width: 15%;
    display: flex;
    flex-direction: column;
    background-color: #071b8b;
    color: white;
}

.blue p{
    height: 50%;
    font-size: 120%;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media only screen and (max-width: 600px) {

    .Header .red,
    .Header .blue {
        display: none;
    }

    .Header .image{
        width: 100%;
    }

}