.navbar{
    width: calc(100vw - 30px);
    height: 10%;
    display: flex;
    align-items: center;
    position: absolute;
    top: calc(10% + 8px);
}

#down-caret{
    font-size: 1.2vw;
}

#plus-icon{
    font-size: 1.2vw;
    display: none;
}

.nav-container{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2%;
    width: 100%;
    height: 50%;
    margin: 0;
    padding: 0;
}

.nav-item{
    width: max-content;
    height: 100%;
    list-style-type: none;
    font-size: 1.2vw;
    position: relative;
    display: flex;
    align-items: center;
}

.nav-item p{
    display: flex;
    cursor: pointer;
    align-items: center;
}

a{
    text-decoration: none;
    color: black;
}

.nav-container ul{
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 20%;
    border-radius: 10px;
    background-color: white;
    z-index: 1;
}

.nav-container ul li{
    width: max-content;
    margin-top: 10px;
    margin-bottom: 10px;
}

.nav-container ul li ul{
    position: absolute;
    top: 100%;
    right: 0 !important;
    width: max-content;
}

.nav-container ul li a:hover{
    color: #cf2c31;
}

.nav-container li ul p{
    margin: 0;
}

.nav-container>li>p,
.nav-container>li>a {
    border-bottom: solid 5px transparent;
}

.nav-container>li>p:hover,
.nav-container>li>a:hover {
    border-bottom: solid 5px #cf2c31;
}

.nav-item > ul{
    display: none;
}

.nav-item:hover > ul{
    display: block;
}

.nav-container ul li > ul{
    position: absolute;
    top: 0;
    transform: translateX(15%);
    display: none;
}

.nav-container ul li:hover > ul{
    display: block;
}
