.footer{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.top-footer{
    width: 100%;
    height: 90%;
    display: flex;
}

.lFooter{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

h3 {
    font-size: 1.8vw;
    color: #071b8b;
    height: 10%;
    margin: 0 0 0 4%;
}

.links{
    display: flex;
    height: 95%;
}

.links1,.links2{
    width: 50%;
    height: 90%;
    display: flex;
    flex-direction: column;
    margin: 1%;
}

.links1 > p{
    font-size: 1.4vw;
    width: 200%;
}

.links1 > a, .links2 > a{
    font-size: 1.5vw;
    margin: 10px;
    display: flex;
    align-items: center;
}

.footer-icons{
    color: #cf2c31;
    margin-right: 10px;
}

.rFooter {
    width: 50%;
}

.rFooter > p{
    font-size: 1.4vw;
    width: 90%;
    margin-left: 4%;
}

.bottom-footer{
    width: 100%;
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: solid;
    background-color: #071b8b;
    color: white;
}

.bottom-footer > *{
    font-size: 1.2vw;
    margin: 1%;
}

#top-icon{
    color: #cf2c31;
}

@media screen and (max-width:600px) {
    h3{
        font-size: 5vw;
    }
    .footer{
        height: max-content;
    }

    .top-footer{
        flex-direction: column;
        height: max-content;
    }

    .lFooter,.rFooter{
        width: 100%;
        height: 50%;
        height: max-content;
    }

    .links{
        height: max-content;
    }

    .links1 > a,.links2 > a,.links1 > p,.rFooter > p{
        font-size: 4vw;
    }

    .bottom-footer{
        width: 100vw;
        border: solid;
    }

    .bottom-footer > *{
        font-size: 5vw;
    }

    #top-icon{
        font-size: 10vw;
    }

}