@media screen and (max-width:600px) {
    
    .navbar{
        border: solid;
        top: 10%;
    }

    #menu-content{
        border: solid;
        display: flex;
        align-items: center;
        width: 50%;
        height: 50%;
        position: absolute;
        top: 0;
        margin-left: 2%;
    }

    .nav-container{
        background-color: white;
        position: fixed;
        top: 0;
        left: 0;
        border: solid 1px black;
        border-radius: 10px;
        width: 80%;
        height: 100vh;
    }

    #cross-content{
        position: absolute;
        top: -50%;
        border: solid;
        width: 80%;
        height: 50%;
        display: flex;
        align-items: center;
        z-index: 100;
    }

    #hamburger,
    #cross-icon,
    .menu {
        font-size: 8vw;
        color: white;
    }

    .menu{
        margin-left: 2%;
    }

    #cross-content > *{
        color: black;
    }

    .menu-items{
        position: absolute;
        top: 10%;
        border: solid;
        width: 90%;
        height: 80%;
    }

}