.courseContainer{
    border: solid 3px #cf2c31;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.courseContainer h2{
    font-size: 3vw;
    color: #071b8b;
}

.courseContainer h3{
    font-size: 2vw;
    color: white;
    background-color: #cf2c31;
    width: 90%;
    border-radius: 10px;
    text-align: center;
}

.reg20,.reg18{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;
    height: 50vh;
}

.ug,.pg{
    width: 50%;
    height: 90%;
}

.ug > p,.pg > p{
    font-size: 1.3vw;
    color: #071b8b;
    font-weight: bold;
}

.ug > ol > li > a,.pg > ol > li > a{
    font-size: 1.2vw;
    line-height: 2vw;
    color: #cf2c31;
}