.header-image{
    width: 100%;
    height: 50%;
}

.header-image img{
    width: 100%;
    height: 90%;
}

.header-image .overlay{
    width: calc(100vw - 30px);
    height: 10%;
    background-color: rgba(255, 255, 255, 0.5);
    position: absolute;
}

@media screen and (max-width: 600px) {
    .header-image img{
        position: absolute;
        top: 10%;
        width: 95%;
        height: 10%;
    }

    .header-image .overlay{
        display: none;
    }
}